body, #app {
  width: 100vw;
  overflow-x: hidden;
  height: 100vh;
  background-image: -webkit-linear-gradient(top, #063f35, #013235, #0d252d, #12181f, #0b0b0c);
  background-image: linear-gradient(to bottom, #063f35, #013235, #0d252d, #12181f, #0b0b0c);
  font-family: 'Francois One', sans-serif; }

.back {
  position: absolute;
  top: 5%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 50vw;
  height: 90vh;
  background: transparent; }
  .back #play {
    position: absolute;
    top: 25%;
    left: 50%;
    cursor: pointer;
    font-size: 2em;
    color: white;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .back .wrap-label {
    position: absolute;
    top: 80%;
    width: 50vw;
    display: flex;
    justify-content: space-around;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }

.label {
  width: calc(13em + 3vw);
  height: 6.5em;
  color: #a6a6a6;
  background: rgba(0, 0, 0, 0.3) !important; }
  .label .head {
    top: 10%;
    text-align: center;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .label .line {
    height: 2px;
    border: none;
    width: calc(13em + 2vw);
    top: 32%;
    font-size: calc(0.7em + 0.3vw);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-image: -webkit-linear-gradient(top, #063f35, #013235, #0d252d, #12181f, #0b0b0c);
    background-image: linear-gradient(to bottom, #063f35, #013235, #0d252d, #12181f, #0b0b0c); }
  .label .inline {
    display: flex;
    justify-content: space-around;
    align-items: flex-start; }
    .label .inline .length {
      font-size: calc(1.2em + 0.3vw);
      cursor: default;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      margin-top: -2%; }
    .label .inline .inc {
      color: #FF6D00;
      padding: 5px;
      cursor: pointer; }
    .label .inline .dec {
      color: #FF6D00;
      padding: 5px;
      cursor: pointer; }

.clock {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: calc(10em + 8vw);
  height: calc(10em + 8vw);
  background-color: transparent;
  border: none;
  border-radius: 50%;
  color: white;
  box-shadow: 0px 3px 5px rgba(244, 244, 244, 0.3) inset;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out; }
  .clock #time {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 4em;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .clock #text {
    position: absolute;
    top: 10%;
    left: 50%;
    font-size: 1em;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .clock .refresh {
    position: absolute;
    top: 88%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: calc(0.5em + 1.5vw);
    cursor: pointer; }

.beat-animation {
  -webkit-animation: beat 2s infinite;
          animation: beat 2s infinite; }

@-webkit-keyframes beat {
  0% {
    -webkit-transform: scale(1) translate(-50%, -50%);
            transform: scale(1) translate(-50%, -50%); }
  50% {
    -webkit-transform: scale(1.2) translate(-40%, -50%);
            transform: scale(1.2) translate(-40%, -50%); }
  100% {
    -webkit-transform: scale(1) translate(-50%, -50%);
            transform: scale(1) translate(-50%, -50%); } }

@keyframes beat {
  0% {
    -webkit-transform: scale(1) translate(-50%, -50%);
            transform: scale(1) translate(-50%, -50%); }
  50% {
    -webkit-transform: scale(1.2) translate(-40%, -50%);
            transform: scale(1.2) translate(-40%, -50%); }
  100% {
    -webkit-transform: scale(1) translate(-50%, -50%);
            transform: scale(1) translate(-50%, -50%); } }

@media only screen and (max-width: 800px) {
  body .clock {
    top: 45%;
    border-width: 2px !important; }
    body .clock .refresh {
      font-size: 3.0vmax; }
    body .clock #text {
      font-size: 0.9em; }
    body .clock #time {
      font-size: 3.1em; }
  body .back {
    top: 2%; }
    body .back #play {
